import { toast } from 'react-toastify';

const handleRegisterSuccess = () => {
  toast.success(
    <div>
      ✅ <b>Cadastro realizado!</b>
      <br />
      <br />
      💌 <b>Acesso será liberado após confirmar o e-mail!</b>
    </div>,
    {
      position: 'top-center',
      autoClose: 4000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    }
  );
};

const handleRegisterError = () => {
  toast.error(
    <div>
      ❌ <b>Falha ao realizar o cadastro!</b>
      <br />
      <br />
      <b>Usuário/E-email já cadastrados!</b>
    </div>,
    {
      position: 'top-center',
      autoClose: 4000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    }
  );
};

