import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import { useStyles } from './styles';

import { api } from '../../../services/api';

import {
  TextField,
  Button,
  Box,
  Typography,
  Checkbox,
  Dialog,
  DialogContent,
  DialogActions,
} from '@material-ui/core';

import {
  handleWarnForCheckButton,
  handleRegisterSuccess,
  handleRegisterError,
} from '../../../utils/Toast';
import ServiceTerm from '../../../utils/Term';
import { AxiosResponse } from 'axios';

type FormProps = {
  name: string;
  surname: string;
  email: string;
  username: string;
  password: string;
};

export function Form() {
  const classes = useStyles();
  const history = useHistory();
  const { handleSubmit, control, reset } = useForm();
  const [open, setOpen] = useState(false);
  const [check, setCheck] = useState(false);

  const onSubmit = async (data: FormProps) => {
    if (!check) {
      return handleWarnForCheckButton();
    }

    await api
      .post('/users', {
        nome: data.name,
        sobrenome: data.surname,
        email: data.email,
        usuario: data.username,
        senha: data.password,
      })
      .then(async (res: AxiosResponse) => {
        if (res.status === 200) {
          setCheck(!check);

          await api
            .post('/mailconfirm', {
              email: data.email,
            })
            .then((response) => {
              if (response.status === 200) {
                handleRegisterSuccess();
              }
            })
            .catch((error) => {
              console.log(error);
              handleRegisterError();
            });

          reset({
            name: '',
            surname: '',
            email: '',
            username: '',
            password: '',
          });
        }
      })
      .catch(() => {
        return handleRegisterError();
      });

    console.log(data);
  };

  return (
    <form className={classes.content} onSubmit={handleSubmit(onSubmit)}>
      {/* Input Name */}
      <Box className={classes.boxName}>
        <Controller
          name="name"
          control={control}
          defaultValue=""
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <TextField
              label="Informe seu nome"
              variant="outlined"
              value={value}
              onChange={onChange}
              error={!!error}
              helperText={error ? error.message : null}
              className={classes.input}
              style={{ marginRight: '0.10rem' }}
            />
          )}
          rules={{
            required: 'Nome é obrigatório',
          }}
        />

        {/* Input Surname */}
        <Controller
          name="surname"
          control={control}
          defaultValue=""
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <TextField
              label="Informe seu sobrenome"
              variant="outlined"
              value={value}
              onChange={onChange}
              error={!!error}
              helperText={error ? error.message : null}
              className={classes.input}
            />
          )}
          rules={{
            required: 'Sobrenome é obrigatório',
          }}
        />
      </Box>
      {/* Input Email */}
      <Controller
        name="email"
        control={control}
        defaultValue=""
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <TextField
            label="Informe seu e-mail"
            variant="outlined"
            value={value}
            onChange={onChange}
            error={!!error}
            helperText={error ? error.message : null}
            className={classes.input}
          />
        )}
        rules={{
          required: 'E-mail é obrigatório',
          pattern: {
            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
            message: 'Informe um formato de e-mail válido Ex: example@mail.com',
          },
        }}
      />

      {/* Input Username */}
      <Controller
        name="username"
        control={control}
        defaultValue=""
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <TextField
            label="Informe seu usuário"
            variant="outlined"
            value={value}
            onChange={onChange}
            error={!!error}
            helperText={error ? error.message : null}
            className={classes.input}
          />
        )}
        rules={{
          required: 'Usuário é obrigatório',
        }}
      />

      {/* Input Password */}
      <Controller
        name="password"
        control={control}
        defaultValue=""
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <TextField
            label="Informe sua senha"
            type="password"
            variant="outlined"
            value={value}
            onChange={onChange}
            error={!!error}
            helperText={error ? error.message : null}
            className={classes.input}
          />
        )}
        rules={{
          required: 'Senha é obrigatório',
          minLength: {
            value: 6,
            message: 'Senha precisa ter no mínimo 6 caracteres',
          },
        }}
      />

      <Box className={classes.boxService}>
        <Checkbox
          checked={check}
          onChange={() => setCheck(!check)}
          name="checkedB"
          color="primary"
        />
        <Typography variant="h6" style={{ fontSize: '0.9rem' }}>
          Li e aceito os{' '}
          <button className={classes.buttonTerm} onClick={() => setOpen(true)}>
            Termos de Serviços
          </button>
          <div>
            <Dialog
              onClose={() => setOpen(false)}
              aria-labelledby="customized-dialog-title"
              open={open}
            >
              <DialogContent dividers>
                <Typography gutterBottom>
                  {/* Component Term of Service */}
                  <ServiceTerm />
                </Typography>
              </DialogContent>
              <DialogActions>
                <Button
                  autoFocus
                  onClick={() => {
                    setOpen(false);
                    setCheck(true);
                  }}
                  color="primary"
                >
                  Aceito
                </Button>
              </DialogActions>
            </Dialog>
          </div>
        </Typography>
      </Box>

      <Button
        variant="contained"
        className={classes.button}
        style={{ backgroundColor: '#64991e' }}
        type="submit"
      >
        Cadastrar
      </Button>
      <Button
        variant="contained"
        className={classes.button}
        style={{
          backgroundColor: '#2E76CF',
          width: '60%',
          marginBottom: '1rem',
        }}
        type="submit"
        onClick={() => history.push('/')}
      >
        Voltar
      </Button>
    </form>
  );
}

export default Form;
